@keyframes fadeInFromLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }

    50% {
        opacity: 0;
    }

    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(720deg);
    }

    to {
        transform: rotate(1440deg);
    }
}

body input,
body h1,
body a,
body button {
    color: #b5b5b6;
    font-weight: unset;
    margin: 0px;
    padding: 0px;
}

#app {
    background-color: #010101;
    height: 100vh;
    overflow: hidden;
}

#app #crypto-list {
    transform: translateX(0);
    position: fixed;
    bottom: 30px;
    height: 330px;
}

#app.list-toggled #crypto-details #crypto-details-content {
    /* margin-left: 358px; */
    /* width: calc(100% -358px); */
    /* padding-top: 200px; */
}

#app .loading-spinner-wrapper {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0px;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 111;
}

#app .loading-spinner-wrapper .loading-spinner {
    animation: spin 2s ease-in-out infinite;
    pointer-events: none;
}

#app .loading-spinner-wrapper .loading-spinner i {
    color: white;
    font-size: 3em;
}

#app #crypto-list {
    animation: fadeInFromLeft 0.25s ease-in;
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: calc(100% - 20px);
    overflow: auto;
    padding: 10px;
    padding-left: 0px;
    position: relative;
    transform: translateX(-520px);
    transition: all 0.25s;
    width: 500px;
    z-index: 2;
    transform: translateX(0);
    position: fixed;
    bottom: 30px;
    height: 330px;
}

#app #crypto-list::-webkit-scrollbar {
    width: 0px;
}

#app #crypto-list .crypto-list-item {
    background-color: transparent;
    border: none;
    border-bottom-right-radius: 1000px;
    border-top-right-radius: 1000px;
    cursor: pointer;
    display: flex;
    outline: none;
    padding: 10px;
    padding-left: 0px;
    position: relative;
    transition: all 0.25s;
}

#app #crypto-list .crypto-list-item:hover .crypto-list-item-content,
#app #crypto-list .crypto-list-item:focus .crypto-list-item-content,
#app #crypto-list .crypto-list-item.selected .crypto-list-item-content {
    width: calc(100% - 120px);
}
.selected h1,.selected button{
    color: #ffffff;
}


#app #crypto-list .crypto-list-item .crypto-list-item-background {
    align-items: center;
    border-radius: 10px;
    border-bottom-right-radius: 1000px;
    border-top-right-radius: 1000px;
    display: flex;
    height: 100%;
    left: 0px;
    overflow: hidden;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 1;
}

#app #crypto-list .crypto-list-item .crypto-list-item-background .crypto-list-item-symbol {
    color: white;
    font-size: 8em;
    font-weight: 500;
    max-width: calc(100% - 160px);
    opacity: 0.1;
    overflow: hidden;
    position: relative;
    text-transform: uppercase;
    white-space: nowrap;
}

#app #crypto-list .crypto-list-item .crypto-list-item-background .crypto-list-item-background-image {
    background-color: #141414;
    border-radius: 1000px;
    height: 120px;
    opacity: 0.25;
    position: absolute;
    right: 20px;
    width: 120px;
    z-index: 2;
}

#app #crypto-list .crypto-list-item .crypto-list-item-content {
    align-items: center;
    backdrop-filter: blur(5px);
    /* background-color: rgba(255, 255, 255, 0.05); */
    border-bottom-right-radius: 1000px;
    border-top-right-radius: 1000px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
    display: flex;
    gap: 20px;
    padding: 10px;
    position: relative;
    transition: all 0.25s;
    white-space: nowrap;
    width: calc(100% - 160px);
    z-index: 2;
    border: 1px solid #373536;
    /* border: 1px solid red; */
}

#app #crypto-list .crypto-list-item .crypto-list-item-content h1 {
    /* color: white; */
    font-size: 20px;
}
.crypto-list-item{
    padding: 2px !important;
}
.crypto-list-item-price{
    font-size: 14px !important;
}
#app #crypto-list .crypto-list-item .crypto-list-item-content .crypto-list-item-image {
    border-radius: 1000px;
    height: 40px;
    width: 40px;
}

#app #crypto-list .crypto-list-item .crypto-list-item-content .crypto-list-item-rank {
    font-size: 1.25em;
    font-weight: 500;
    text-align: right;
    width: 40px;
}

#app #crypto-list .crypto-list-item .crypto-list-item-content .crypto-list-item-details {
    display: flex;
    flex-direction: column;
    gap: 2px;
    text-align: left;
    width: 240px;
}

#app #crypto-list .crypto-list-item .crypto-list-item-content .crypto-list-item-details .crypto-list-item-name {
    font-size: 20px;
}

#app #crypto-details {
    animation: fadeInFromLeft 0.25s ease-in;
    height: 100vh;
    left: 0px;
    position: fixed;
    top: 0px;
    width: 100vw;
    z-index: 1;
}

#app #crypto-details.transitioning #crypto-details-content #crypto-fields {
    opacity: 0;
    transform: translateX(-20px);
}

#app #crypto-details.transitioning #crypto-details-content #crypto-details-background-image {
    opacity: 0;
    transform: translateY(-20px);
}

#app #crypto-details.transitioning #crypto-details-content #crypto-details-symbol {
    opacity: 0;
    transform: translateY(20px);
}

#app #crypto-details #crypto-details-content {
    /* border-left: 1px solid rgba(255, 255, 255, 0.05); */
    height: 100%;
    margin-left: 0px;
    min-width: 300px;
    position: relative;
    transition: all 0.25s;
    width: calc(100% - 1px);
}

#app #crypto-details #crypto-details-content #crypto-fields  {
    display: inline-flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    padding-left: 40px;
    padding-top: 20px;
    position: relative;
    transition: all 0.25s;
    z-index: 3;
}
#app #crypto-details #crypto-details-content #crypto-fields > div{
    display: inline-flex;
}
#app #crypto-details #crypto-details-content #crypto-fields .crypto-field {
    white-space: nowrap;
    margin-right: 30px;
}

#app #crypto-details #crypto-details-content #crypto-fields .crypto-field.positive .crypto-field-value {
    color: #4caf50;
}

#app #crypto-details #crypto-details-content #crypto-fields .crypto-field.negative .crypto-field-value {
    color: #c62828;
}
.positive{
    color: #4caf50;
}
.negative{
    color: #c62828;
}
