.boxs,
.boxs_x,
.boxs_y {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.boxs_x {
    display: flex;
    flex-direction: column;
}

.boxs_x div {

    width: 100%;
    height: 61.8px;
    border-bottom: 1px rgba(255,255,255, 0.1) solid;
}

.boxs_y {
    display: flex;
    flex-wrap: wrap;
}

.boxs_y div {
    width: 100px;
    height: 100%;
    border-left: 1px rgba(255,255,255, 0.1) dashed;
}

#app #crypto-details #crypto-details-content #crypto-fields .crypto-field .crypto-field-value {
    /* color: white; */
    font-size: 20px;
}

#app #crypto-details #crypto-details-content #crypto-fields .crypto-field .crypto-field-label {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.8em;
    font-weight: 500;
    text-transform: uppercase;
}

#app #crypto-details #crypto-details-content #crypto-price-chart-wrapper {
    height: 100%;
    left: 0px;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 2;


}

#app #crypto-details #crypto-details-content #crypto-price-chart-wrapper #crypto-price-chart-loading-spinner {
    bottom: 0px;
    height: 100px;
    left: 0px;
    position: absolute;
    width: 100px;
    z-index: 2;
}

#app #crypto-details #crypto-details-content #crypto-price-chart-wrapper #crypto-price-chart-loading-spinner .loading-spinner i {
    font-size: 2em;
}

#app #crypto-details #crypto-details-content #crypto-price-chart-wrapper #crypto-price-chart {
    height: 100%;
    position: relative;
    width: 100%;
}

#app #crypto-details #crypto-details-content #crypto-details-symbol {
    bottom: 0px;
    color: white;
    filter: blur(5px);
    font-size: 20em;
    font-weight: 500;
    left: 0px;
    margin: 20px;
    opacity: 0.15;
    position: absolute;
    text-transform: uppercase;
    transition: all 0.25s;
    z-index: 1;
}

#app #crypto-list-toggle-button {
    background-color: #1e1e1e;
    border: none;
    border-radius: 100px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
    bottom: 0px;
    cursor: pointer;
    margin: 20px;
    padding: 20px;
    position: fixed;
    right: 0px;
    z-index: 3;
    display: none;
}

#app #crypto-list-toggle-button:hover i,
#app #crypto-list-toggle-button:focus i {
    color: #1e88e5;
}

#app #crypto-list-toggle-button i {
    color: white;
    font-size: 2em;
    height: 30px;
    line-height: 30px;
    text-align: center;
    width: 30px;
}

#app #youtube-link {
    align-items: center;
    border-radius: 6px;
    display: flex;
    gap: 10px;
    margin: 10px;
    padding: 10px;
    position: absolute;
    right: 0px;
    text-decoration: none;
    top: 0px;
    z-index: 1;
}

#app #youtube-link:hover,
#app #youtube-link:focus {
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, 0.1);
}

#app #youtube-link i {
    color: #c62828;
    font-size: 1.5em;
}

#app #youtube-link h1 {
    color: white;
    font-size: 1.25em;
}

